.sb-collapsible-panel {
  border: var(
    --sb-collapsible-panel-border,
    solid 1px var(--sbt-theme-tertiary-color-20)
  );
  width: 100%;
}

.sb-collapsible-panel__borderless {
  border: none;
}

.sb-collapsible-panel__title {
  align-items: flex-start;
  background: none;
  border: none;
  border-bottom: var(--sb-collapsible-panel-title-border, none);
  color: var(--sbt-theme-tertiary-color-60);
  display: flex;
  font-size: var(--sbt-font-size-35);
  font-weight: var(--sbt-font-weight-60);
  line-height: var(--sbt-line-height-50);
  padding: var(--sbt-spacing-40);
  text-align: left;
  width: 100%;

  &:focus {
    outline: 0;
  }

  &:hover,
  &[aria-expanded='true'],
  &--open {
    color: var(--sbt-theme-tertiary-color-90);
  }
}

.sb-collapsible-panel__title-clickable {
  cursor: pointer;
}

.sb-collapsible-panel__button {
  background: none;
  border: none;
  color: var(--sbt-theme-tertiary-color-60);
  font-size: var(--sbt-font-size-35);
  font-weight: var(--sbt-font-weight-60);
  line-height: var(--sbt-line-height-50);
  padding: 0;

  &--open {
    color: var(--sbt-theme-tertiary-color-90);
  }
}

.sb-collapsible-panel__icon {
  align-self: flex-start;
  cursor: pointer;
  line-height: var(--sbt-line-height-50);
  margin-right: 0.75rem;
  -moz-transition: all 0.3s linear;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;

  &--down {
    -moz-transform: rotate(var(--sb-collapsible-panel-rotation, 90deg));
    -webkit-transform: rotate(var(--sb-collapsible-panel-rotation, 90deg));
    transform: rotate(var(--sb-collapsible-panel-rotation, 90deg));
  }
}

.sb-collapsible-panel__content {
  height: auto;
  max-height: 9999px;
  overflow: hidden;
  transition: visibility 0s linear, max-height 0.3s cubic-bezier(1, 0, 1, 0);
  visibility: visible;

  &--collapsed {
    max-height: 0;
    transition: visibility 0.3s linear, max-height 0.3s cubic-bezier(0, 1, 0, 1);
    visibility: hidden;
  }
}

.sb-collapsible-panel__content-inner {
  padding: var(--sb-collapsible-panel-inner-padding, 0 1rem 1rem 2.6rem);
}

.sb-collapsible-panel-content li {
  margin-left: 1.25rem;
  padding-left: 0.25rem;
}

.sb-collapsible-panel-content ul {
  list-style: disc;
}

.sb-collapsible-panel-content ol {
  list-style: decimal;
}

.sb-collapsible-panel__toggle-alignment-left {
  justify-content: flex-start;
}

.sb-collapsible-panel__toggle-alignment-center {
  justify-content: center;
}

.sb-collapsible-panel__toggle-alignment-right {
  justify-content: flex-end;
}

.sb-collapsible-panel__title--selectable {
  cursor: pointer;

  &.sb-collapsible-panel__title--selected,
  &:hover {
    --sb-typography-color: var(--sb-badge-color, var(--sbt-theme-color-90));
    background-color: var(--sbt-theme-color-30);
    border-radius: var(--sb-button-border-radius);
  }
}
